.tech-section {
  padding: 0.25rem 0 0.25rem 0;
}

.tech-title {
  font-size: 2.5rem;
  font-weight: 800;
  color: #073B4C;
  margin-bottom: 3rem;
  text-align: center;
}

.tech-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.tech-cta {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.tech-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.9rem 2.5rem;
  border-radius: 9999px;
  background: linear-gradient(45deg, #4F871B, #74C627);
  color: #0f172a;
  font-weight: 600;
  font-size: 1.05rem;
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: 0 6px 18px rgba(116, 198, 39, 0.35);
}

.tech-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(116, 198, 39, 0.45);
  color: white;
  text-decoration: none;
}

.tech-card {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 16px;
  padding: 1.5rem;
  text-align: center;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.tech-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.9);
}

.tech-icon {
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
}

.tech-name {
  font-size: 1.25rem;
  font-weight: 700;
  color: #374151;
  margin-bottom: 0.75rem;
}

.tech-description {
  color: #6b7280;
  line-height: 1.6;
  font-size: 0.95rem;
}

.deployment-info {
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 2px solid rgba(7, 59, 76, 0.1);
}

.deployment-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16px;
  padding: 2rem;
  color: white;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.deployment-icon {
  color: #fbbf24;
  flex-shrink: 0;
}

.deployment-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.deployment-content p {
  opacity: 0.9;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.deployment-links {
  display: flex;
  gap: 1rem;
}

.health-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.health-link:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
  color: white;
  text-decoration: none;
}

.link-icon {
  color: #10b981;
}

@media (max-width: 768px) {
  .tech-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .tech-cta {
    margin-top: 1.5rem;
  }
  
  .tech-card {
    padding: 1.25rem;
  }
  
  .deployment-card {
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
  }
  
  .deployment-links {
    justify-content: center;
  }
}
